
@use "./styles/colors";

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main {
  padding-top: 16px;
  flex-grow: 1;
  overflow: scroll;
}

.main-bottomspace {
  height: 10px;
}

.bottombar {
  flex-grow: 0;
  flex-shrink: 0;
  border-top: 1px solid lightgrey;
  padding-bottom: 8px;
  background: colors.$surface;
}

.bottombar-section {
  margin-top: 4px;
  margin-bottom: 8px;
}
